nav{box-shadow:1px 0 10px #00000010}nav .navLeft{display:grid}nav .navLeft .navLeftTop{position:relative;min-height:56px;z-index:1;background:var(--c-light-variant)}nav .navLeft .navLeftTop img{width:4rem;position:relative;top:28px;-webkit-transform:translate(0, -50%);transform:translate(0, -50%)}nav .navLeft .navLeftTop .fiMenu{position:absolute;top:28px;left:50px;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);width:3rem;height:3rem;stroke:var(--c-dark-variant)}nav .navLeft .navLeftTop.showMenu~.navLeftBottom{max-height:300px;transition:700ms ease-in-out}nav .navLeft .navLeftBottom{max-height:0;overflow:hidden;transition:400ms ease-in-out}nav .navLeft .navLeftBottom ul li{font-size:1.6rem;text-align:left;padding:15px 0 10px 20px;border-bottom:solid 2px rgba(141,143,154,.1);margin:2px 0}nav .navRight{height:48px;display:flex;align-items:center;justify-content:center}nav .navRight .searchContainer{width:80%;display:inline-block;border-bottom:2px solid var(--c-dark-variant);position:relative;padding-bottom:10px}nav .navRight .searchContainer input{width:100%;text-align:left;font-size:1.4rem}nav .navRight .searchContainer svg{width:2.3rem;height:2.3rem;position:absolute;right:0;bottom:0;-webkit-transform:translate(0, -5px);transform:translate(0, -5px)}@media(min-width: 768px){nav{min-height:80px;display:flex;justify-content:space-between;align-items:center}nav .navLeft{display:flex;flex-direction:row;align-items:center}nav .navLeft .navLeftTop{min-height:auto;display:flex;align-items:center}nav .navLeft .navLeftTop .fiMenu{display:none}nav .navLeft .navLeftTop img{width:4rem;margin:0 40px;top:0;-webkit-transform:translate(0);transform:translate(0)}nav .navLeft .navLeftBottom{max-height:none}nav .navLeft .navLeftBottom ul{display:flex;height:100%;justify-self:left}nav .navLeft .navLeftBottom ul li{padding:0 20px;display:flex;align-items:center;border:none}nav .navLeft .navLeftBottom ul li a{font-size:1.6rem}nav .navRight{height:100%}nav .navRight .searchContainer{display:flex;align-items:flex-end;border:none;padding:0}nav .navRight .searchContainer input{padding-right:100px}nav .navRight .searchContainer::after{content:"";display:block;position:absolute;background:#1D1F2290;height:1px;width:100%;bottom:-5px}nav .navRight .searchContainer svg{width:2rem;height:2rem;position:relative;-webkit-transform:translate(0, 0);transform:translate(0, 0)}nav .navRight .bsCart2{width:2rem;height:2rem;margin:0 40px}}
.itemContainer{overflow:hidden;margin:20px;padding:20px;box-shadow:0 0 5px #00000020;display:flex;flex-direction:column;justify-content:space-around;min-width:120px;width:150px;height:250px}.itemContainer img{object-fit:contain}.itemContainer h3{text-align:left;font-size:1.2rem}.itemContainer h4{text-align:left;font-size:1.3rem;font-weight:500}@media(min-width: 768px){.itemContainer{min-width:250px;width:250px;height:450px}.itemContainer img{object-fit:contain}.itemContainer h3{font-size:1.6rem}.itemContainer h4{font-size:1.6rem}}

main,nav{width:100%}@media(min-width: 1024px){main,nav{padding:0 100px}}@media(min-width: 1440px){main,nav{padding:0 200px}}
/*Colors*/
:root {
  /*BRAND*/
  --c-primary: #5ECE7B;
  --c-primary-variant: #9EE2B0;
  --c-secondary: #1D1F22;
  --c-secondary-variant: #43464E;

  /*BASE*/
  --c-light: #F1F2F3;
  --c-light-variant: #FFFFFF;
  --c-gray: #72757E;
  --c-gray-variant: #8D8F9A;
  --c-dark: #1D1F22;
  --c-dark-variant: #43464E;

  /*CONTENT*/
  --c-info: #0468DB;
  --c-info-variant: #B4D2F4;
  --c-success: #5ECE7B;
  --c-success-variant: #9EE2B0;
  --c-warning: #ECC713;
  --c-warning-variant: #F6E389;
  --c-danger: #D12727;
  --c-danger-variant: #FCEDE8;

  /*FONT*/
  --c-body: #FFFFFF;
  --c-text: #1D1F22;
  --c-text-muted: #72757E;
  --c-link: #43464E;
  --c-link-hover: #1D1F22;
}

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-align: center;
  text-decoration: none;
  color: #1D1F22;
  color: var(--c-text);
  font-family: 'Raleway', sans-serif;
  font-weight: normal;
}

input{
  border: none;
  background: transparent;
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

html {
  font-size: 62.5%;
}

.App {
  overflow: hidden;
}
